import React from "react";
import { useState, useEffect } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
} from "@vkontakte/vkui";

import {
  InventoryItem,
  MiningInventoryItem,
  ListUsers,
  ExchangeSell,
  ExchangeBuyItem
} from "../components";

import { formatCoins } from "../helpers";

const Exchange = ({
  id,
  dataInventory,
  targetIdInventory,
  openUseItem,
  scheme,
  goHome,
  connectTwoDevices,
  stub,
  socketReconnect,
  networkStatus,
  openBuyItem,
  updateConnection,
  socketReconnection,
  snackbar,
  dataMiners,
  miners,
  durkaCoins,
  course,
  dataExchangeBuy,
  openExchangeBuy
}) => {
  const [activeTab, setActiveTab] = useState("buy");

  function tabBuy() {
    setActiveTab("buy");
    window.scrollTo(0, 0);
  }

  function tabSell() {
    setActiveTab("sell");
    window.scrollTo(0, 0);
  }




  const renderExchangeBuy = dataExchangeBuy.map((item, index) => {
    return (
      <ExchangeBuyItem
        key={index}
        id={index + 1}
        idVk={item.idVk}
        firstName={item.firstName}
        lastName={item.lastName}
        photoURL={item.photoURL}
        amountDC={item.amountDC}
        costOneDC={item.costOneDC}
        remaininAmount={item.remaininAmount}
        openExchangeBuy={openExchangeBuy}
      />
    );
  });

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
        left={<PanelHeaderBack onClick={goHome} data-to="home" />}
      >
        
      </PanelHeaderSimple>

      {connectTwoDevices ? (
        stub(
          "   2  ",
          socketReconnect,
          ""
        )
      ) : (
        <>
          <FixedLayout vertical="top" filled={true}>
            <Tabs>
              <TabsItem onClick={tabBuy} selected={activeTab === "buy"}>
                
              </TabsItem>
              <TabsItem onClick={tabSell} selected={activeTab === "sell"}>
                
              </TabsItem>
            </Tabs>
          </FixedLayout>

          {networkStatus ? (
            stub(
              "  - !",
              updateConnection,
              ""
            )
          ) : (
            <>
              {socketReconnection ? (
                stub(
                  "   ",
                  socketReconnect,
                  ""
                )
              ) : (
                <>
                  {activeTab === "buy" ? (
                    <>
                      {/* {renderInventory.length
                                                ? */}
                      <Div style={{ marginTop: 55 }}>
                        {renderExchangeBuy}
                      </Div>
                      {/* // : */}
                      {/* <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          flexDirection: "column",
                        }}
                      >
                        <Spinner size="large" style={{ marginTop: 90 }} />
                      </div> */}
                      {/* // } */}
                    </>
                  ) : (
                    ""
                  )}

                  {activeTab === "sell" ? (
                    <>
                      <Div
                        style={{
                          marginTop: 55,
                        }}
                      >
                        <ExchangeSell
                          durkaCoins={formatCoins(durkaCoins)}
                          course={course}
                        />
                      </Div>
                    </>
                  ) : (
                    ""
                  )}
                </>
              )}

              {snackbar}
            </>
          )}
        </>
      )}
    </Panel>
  );
};

export { Exchange };
