import React from "react";
import { Cell, Div, Avatar, Link } from "@vkontakte/vkui";
import { getRankByRating, formatCoins } from "../helpers";

const ExchangeBuyItem = ({
  id,
  firstName,
  lastName,
  amountDC,
  photoURL,
  costOneDC,
  idVk,
  remaininAmount,
  openExchangeBuy,
}) => {
  const urlUser = "https://vk.com/id" + idVk;
  const sumRUB = amountDC * costOneDC;
  const remaininAmountRUB = remaininAmount * costOneDC;

  return (
    <Div className="card">
      <span className="listNumber">{id}</span>

      <Cell
        className="myItem"
        before={
          photoURL ? (
            <Link href={urlUser} target="_blank">
              <Avatar src={photoURL} />
            </Link>
          ) : (
            <Avatar src={"https://vk.com/images/camera_200.png"} />
          )
        }
        description={
          <Div className="div">
            <Div
              style={{
                padding: 2,
                margin: 0,
                display: "flex",
                alignItems: "center",
                flexDirection: "row",
              }}
            >
              <Div className="div" style={{ whiteSpace: "nowrap" }}>
                1
              </Div>
              <Div className="iconDC" style={{ marginLeft: 3 }} />
              <Div className="div" style={{ whiteSpace: "nowrap" }}>
                = {costOneDC} 
              </Div>
            </Div>
            <Div
              style={{
                padding: 2,
                margin: 0,
                display: "flex",
                alignItems: "center",
                flexDirection: "row",
              }}
            >
              <Div> {amountDC}</Div>
              <Div className="iconDC" style={{ marginLeft: 3 }} />
              <Div>({sumRUB} )</Div>
            </Div>
            <Div
              style={{
                padding: 2,
                margin: 0,
                display: "flex",
                alignItems: "center",
                flexDirection: "row",
              }}
            >
              <Div>  : {remaininAmount}</Div>
              <Div className="iconDC" style={{ marginLeft: 3 }} />
              <Div>({remaininAmountRUB} )</Div>
            </Div>
          </Div>
        }
        asideContent={
          <Div id={id} onClick={openExchangeBuy} style={{ color: "green" }}>
            
          </Div>
        }
        // before={<Icon24FavoriteOutline/>}
        // indicator={rating}
        // indicator={<Icon24FavoriteOutline fill="black"/>}
      >
        {firstName + " " + lastName}
      </Cell>
    </Div>
  );
};

export { ExchangeBuyItem };
