import React from 'react';
import { ModalRoot, ModalCard, InfoRow, FormLayout, Input,} from '@vkontakte/vkui';

const ModalShopInfoItem = ({iconItem, nameItem, ratingItem, 
  timeItem, targetIdShop, setActiveModal }) => {


    return (
      <ModalRoot activeModal="info">
      <ModalCard
        id="info"
        onClose={() => setActiveModal(null)}
        icon={iconItem}
        header={nameItem}
        caption={
          <>
            {targetIdShop >= 1 && targetIdShop <= 3 ? (
              <>
                <InfoRow>
                  {nameItem} -    +{ratingItem} 
                  .
                </InfoRow>
              </>
            ) : nameItem === "" ? (
              <>
                <InfoRow>
                  {nameItem} -     {timeItem}, 
                    ,      
                  ' '  ' '
                </InfoRow>
              </>
            ) : nameItem === " " ? (
              <>
                <InfoRow>
                  {nameItem} -  {ratingItem}    
                  
                </InfoRow>
              </>
            ) : nameItem === " " ? (
              <>
                <InfoRow>
                  {nameItem} -  {ratingItem}    
                  .
                </InfoRow>
              </>
            ) : nameItem === " " ? (
              <>
                <InfoRow>
                  {nameItem} -   {ratingItem} DC/.
                </InfoRow>
              </>
            ) : (
              ""
            )}
          </>
        }
      ></ModalCard>
    </ModalRoot>
    );
};

export { ModalShopInfoItem };

