import React from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  Group,
  Header,
  Cell,
  Div,
  Link,
  InfoRow,
  Input,
  FormLayoutGroup,
  FormLayout,
  Button,
} from "@vkontakte/vkui";

const ExchangeSell = ({ go, id, durkaCoins, course }) => {
  return (
    <Div
      style={{
        margin: 10,
      }}
    >
      <Group>
        <Cell>
          <InfoRow header=" " className="balanceDCMiddle">
            <Div className="divFlexStart">
              <Div className="balanceDCMiddle">{durkaCoins}</Div>
              <Div className="iconDC" style={{ marginLeft: 4 }} />
            </Div>
          </InfoRow>

          <InfoRow className="balanceDCMiddle" header=" ">
            <Div className="divFlexStart">
              <Div className="iconDC" style={{ marginRight: 4 }} />
              <Div className="balanceDCMiddle"> = {course}/ </Div>
            </Div>
          </InfoRow>

          <InfoRow header=" ">
            <Div style={{ color: "green" }}>+25  </Div>
          </InfoRow>

          <Div
            style={{
              marginBottom: 5,
              marginTop: 5,
            }}
          >
            <Div
              style={{
                marginBottom: 5,
                marginTop: 5,
              }}
            >
               - DurkaCoins  
            </Div>
            <Input type="number"></Input>

            <Div
              style={{
                marginBottom: 5,
                marginTop: 5,
              }}
            >
                 1 DurkaCoins  
            </Div>
            <Input type="number"></Input>
          </Div>

          <Div
            style={{
              marginBottom: 5,
              marginTop: 10,
            }}
          >
            <Button mode="commerce" size="xl" stretched>
               
            </Button>
          </Div>
        </Cell>
      </Group>
    </Div>
  );
};

export { ExchangeSell };
