import React from "react";
import { Cell, Div, Group, Header } from "@vkontakte/vkui";

import styles from "../styles/custom-styles.sass";

/**
 * @param {Object} params - 
 * @param {number} params.id -  
 * @param {string} params.name -  
 * @param {number} params.ratingMin -  
 * @param {number} params.ratingMax -  
 * @param {number} params.pillBlue -     
 * @param {number} params.pillRed -     
 * @param {number} params.pillGold -     
 * @param {number} params.shirt -     
 * @param {number} params.cell -     
 * @param {number} params.vacation -    
 */
const RanksItem = ({
  id,
  name,
  ratingMin,
  ratingMax,
  pillBlue,
  pillRed,
  pillGold,
  shirt,
  cell,
  vacation,
}) => {
  return (
    <Group header={<Header mode="secondary">{name}</Header>}>
      <Cell
        style={{ margin: 0, padding: 0 }}
        asideContent={`${ratingMin} - ${ratingMax || Infinity}`}
      >
        {name.toLowerCase() === "" ? (
          <Div>  .</Div>
        ) : (
          <>
            <Div>  :</Div>
            {pillBlue ? <Div>Blue : {pillBlue} </Div> : ""}
            {pillRed ? <Div>Red : {pillRed} </Div> : ""}
            {pillGold ? <Div>Gold : {pillGold} </Div> : ""}
            {vacation ? <Div>: {vacation} </Div> : ""}
            {shirt ? <Div> : {shirt} </Div> : ""}
            {cell ? <Div> : {cell} </Div> : ""}
          </>
        )}
      </Cell>
    </Group>
  );
};

export { RanksItem };
