import React from 'react';
import { ModalRoot, ModalCard, InfoRow, FormLayout, Input,} from '@vkontakte/vkui';

const ModalShopBuyItem = ({iconItem, nameItem, ratingItem, onChangeHandlerAmount, 
  timeItem, buyItem, targetIdShop, setActiveModal }) => {


    return (
      <ModalRoot activeModal="info">
      <ModalCard
        id="info"
        onClose={() => setActiveModal(null)}
        icon={iconItem}
        header={nameItem}
        caption={
          <>
            {targetIdShop >= 1 && targetIdShop <= 3 ? (
              <>
                <InfoRow>
                  {nameItem} -    +{ratingItem} 
                  .
                </InfoRow>
                <FormLayout>
                  <Input
                    top={` - ${nameItem}`}
                    placeholder="1"
                    onChange={onChangeHandlerAmount}
                    type="number"
                  />
                </FormLayout>
              </>
            ) : nameItem === "" ? (
              <>
                <InfoRow>
                  {nameItem} -     {timeItem}, 
                    ,      
                  ' '  ' '
                </InfoRow>
                <FormLayout>
                  <Input
                    top={` - ${nameItem}`}
                    placeholder="1"
                    onChange={onChangeHandlerAmount}
                    type="number"
                  />
                </FormLayout>
              </>
            ) : nameItem === " " ? (
              <>
                <InfoRow>
                  {nameItem} -  {ratingItem}    
                  
                </InfoRow>
                <FormLayout>
                  <Input
                    top={` - ${nameItem}`}
                    placeholder="1"
                    onChange={onChangeHandlerAmount}
                    type="number"
                  />
                </FormLayout>
              </>
            ) : nameItem === " " ? (
              <>
                <InfoRow>
                  {nameItem} -  {ratingItem}    
                  .
                </InfoRow>
                <FormLayout>
                  <Input
                    top={` - ${nameItem}`}
                    placeholder="1"
                    onChange={onChangeHandlerAmount}
                    type="number"
                  />
                </FormLayout>
              </>
            ) : (
              ""
            )}
          </>
        }
        actions={[
          {
            title: "",
            mode: "commerce",
            action: () => {
              buyItem();
            },
          },
        ]}
      ></ModalCard>
    </ModalRoot>
    );
};

export { ModalShopBuyItem };

